
.fog{
    background-color: rgb(255, 255, 255, 0.7);
}

.box {
    display: flex;
    flex-flow: column;
    height: 100%;
}
.box .row.header {
    flex: 0 1 auto;
}
.box .row.content {
    flex: 1 1 auto;  
}
.box .row.footer {
    flex: 0 1 0px;
}
.box-container {
    display: contents;
}

.contentBox{
    display: flex;
    flex-flow: column;
    height: min-content;
    /* background-color: blue; */
}


.nameStyle{
    color: rgb(62, 155, 231);
    font-weight: 300;
    font-size: 50px;
    float: left;
    position: static;
    opacity: 1;
    padding: 10px 20px;
    font-family: 'Century Gothic';
}

.sidebar_button_text{
    color: rgb(120, 186, 240);
    font-weight: 300;
    font-size: 40px;
    font-family: 'Century Gothic';
}

.content_Text{
    font-size: 12px;
    color: black;
}

.grid_layout{
    display: grid;
    height: 100%;
    columns: 2;
    flex: 1;
    grid-template-columns: 1fr 4fr;
    justify-content: center;

}

.sidebar_button_container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 30px;
}

.sidebar_buttons{
    background-color: rgb(255, 255, 255, 0);
    margin: 5px 15px;
    border-radius: 5px;
    /* box-shadow: 2px 2px 2px 0px lightgray; */
    outline: 0px;
    border: none;
    height: 60px;
    transition: ease background-color 250ms;
    &:hover{
        background-color: rgba(185, 221, 245, 0.1);
        box-shadow: inset 2px 2px 2px 2px lightgrey;
        /* box-shadow: 5px 5px 5px 0px lightgrey; */
    }

}

.hyperlink_button{
    background-color: rgba(255, 255, 255, 0);
    color: black;
    margin: 5px 15px;
    border-radius: 20px;
    box-shadow: 2px 2px 2px 0px lightgray;
    outline: 0px;
    border: none;
    height: 60px;
    width: 200px;
    transition: ease background-color 250ms;
    &:hover{
        background-color: rgba(185, 221, 245, 0.1);
        box-shadow: inset 2px 2px 2px 2px lightgrey;
    }

}

.hyperlink_button_text{
    color: rgb(120, 186, 240);
    font-weight: 200;
    font-size: 30px;
    font-family: 'Century Gothic';
    
}


.content_holder{
    height: 770px;
    flex: 1;
    margin: 30px;
    padding: 30px;
    overflow: auto;
    overflow-y: scroll;
}

.project_Title{
    font-size: 50px;
    text-shadow: 1px 1px lightgray;
    font-family: 'Century Gothic';
    color: rgb(29, 91, 145);
}

.portfolio_heading{
    font-size: 30px;
    text-shadow: 2px 2px lightgray;
    font-family: 'Century Gothic';
    color: rgb(10, 115, 206);
}

.portfolio_text{
    padding: 10px;
    font-size: 18px;
    text-shadow: 1px 1px lightgray;
    font-family: 'Century Gothic';
    color: rgb(8, 10, 119);
    /* background-color: pink; */
}

.skillName{
    text-align: center;
    vertical-align: middle;
    height: 100%;
}

.skillFog{
    background-color: rgb(255, 255, 255, 0.3);

}

.resumeGrid{
    display: grid;
    padding: 5px;
    row-gap: 10px;
    /* background-color: pink */
}

.gridRow{
    display: grid;
    height: 100%;
    flex: 1;
    grid-template-columns: 1fr 4fr;
    justify-content: center;
    column-gap: 10px;
    /* background-color: rgb(196, 90, 196); */
}

.aboutRow{
    display: grid;
    padding: 10px;
    height: 100%;
    flex: 1;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
}

.projectsGridRow{
    display: grid;
    padding: 5px;
    height: 100%;
    flex: 1;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: center;
    column-gap: 20px;
}


.projectCard{
    background-color: rgb(255, 255, 255);
    margin: 5px 5px;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 0px lightgray;
    outline: 0px;
    border: none;
    height: 450px;
    width: 100%;
    transition: ease background-color 250ms;
    &:hover{
        background-color: rgb(196, 248, 255);
        box-shadow: 5px 5px 5px 0px lightgrey;
    }
}

.cardsplit{
    display: grid;
    margin: 5px 0px 0px 0px;
    width: auto;
    height: 100%;
    flex: 1;
    grid-template-rows: 7fr 3fr;
    justify-content: center;
}

.card_topHalf{
    height: 100%;
    width: 100%;
    /* background-color: rgb(38, 55, 216); */

}

.card_bottomHalf{
    height: 100%;
    width: 100%;    
    /* background-color: rgba(216, 38, 83, 0.89); */
    
}

.cardTitle{
    font-size: 30px;
    font-weight: 300;
    text-shadow: 1px 1px lightgray;
    width: 100%;
}

.cardSubtitle{
    color: gray;
    font-weight: 100;
    font-size: 18px;
    width: 300px;
  
}

.lineBreak{
    margin: 2px;
    color: rgb(120, 186, 240);
    background-color: rgb(120, 186, 240);
    height: 2px;
    grid-column: span 2;
}


.myPhoto{
    width: 300px;    

}

.photoContainer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.toolboxRow{
    display: grid;
    padding: 5px;
    height: 100%;
    flex: 1;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;

}

.projectPhoto{
    height: 300px;

}

.projects_page_GridRow{
    display: grid;
    padding: 5px;
    height: 100%;
    width: 100%;
    flex: 1;
    row-gap: 5px;
    grid-template-columns: 1fr 1fr 1fr  ;
    justify-content: center;
    align-content: center;
    column-gap: 20px;
}

.projects_Experiencepage_GridRow{
    display: grid;
    padding: 5px;
    width: 100%;
    flex: 1;
    row-gap: 5px;
    grid-template-columns: 1fr 1fr;
    justify-content: middle;
    align-content: middle;

}

.projectPhoto_zineCovers{
    width: 100%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: span 2;
}

.projectPhoto_zineLEFT{
    width: 100%;
    height: 300px;
    display: flex;
    align-items: end;
    justify-content: end;

}

.projectPhoto_zineRIGHT{
    width: 100%;
    height: 300px;
    display: flex;
    align-items: start;
    justify-content: start;

}



